<template>
  <div>
    <div>
      <b-card id="settingsCard" class="borderAlign" no-body>
        <b-tabs v-model="tabIndex">
          <b-tab
            :title-link-class="linkClass(0)"
            :title="$t('ACCOUNTINFORMATION')"
          >
            <b-form>
              <b-row>
                <b-col cols="12" xl="6">
                  <b-col cols="12" class="mt-1">
