                    <label class="highLightHeaderLabel">{{
                      $t("LOGINID")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-3">
                    <b-form-input
                      v-model.trim="$v.accountSettingInfo.loginId.$model"
