                    ></b-form-input>
                    <show-errors
                      :control="$v.accountSettingInfo.loginId"
                      :field="$t('LOGINID')"
                    />
                    <p class="error">{{ validateLoginId }}</p>
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6"></b-col>
                <b-col cols="12" xl="6">
                  <b-col cols="12" class="mt-1">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("LOGINPASSWORD2")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-3">
                    <b-form-input
                      v-model="$v.accountSettingInfo.password.$model"
                      maxlength="15"
                      type="password"
                      class="textFieldClass"
                    ></b-form-input>
                    <show-errors
                      :control="$v.accountSettingInfo.password"
                      :field="$t('LOGINPASSWORD2')"
