                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12" xl="12">
                  <b-button
                    @click="accountInfoCheck"
                    variant="primary"
                    class="px-4 ml-3"
                    >{{ $t("TOBASICINFORMATION") }}</b-button
                  >
                </b-col>
              </b-row>
            </b-form>
          </b-tab>
          <b-tab
            :title-link-class="linkClass(1)"
