                    ></b-form-input>
                    <show-errors
                      :control="$v.basicSettingInfo.companyName"
                      :field="$t('COMPANYNAMEORNAME')"
                    />
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6">
                  <b-col cols="12" class="mt-1">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("PERSONINCHARGE")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-3">
                    <b-form-input
                      v-model="$v.basicSettingInfo.personInCharge.$model"
                      maxlength="50"
                      class="textFieldClass"
                    ></b-form-input>
                    <show-errors
                      :control="$v.basicSettingInfo.personInCharge"
                      :field="$t('PERSONINCHARGE')"
                    />
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12" xl="6">
                  <b-col cols="12" class="mt-1">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="font-weight-bold">{{ $t("PHONENO") }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-3">
                    <b-form-input
                      v-model.trim="$v.basicSettingInfo.phoneNo.$model"
                      maxlength="11"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.basicSettingInfo.phoneNo"
                      :field="$t('PHONENO')"
                    />
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6">
                  <b-col cols="12" class="mt-1">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="font-weight-bold">{{
                      $t("MAILADDRESS")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-3">
                    <b-form-input
                      v-model.trim="$v.basicSettingInfo.email.$model"
                      maxlength="254"
                      class="textFieldClass"
                    ></b-form-input>
                    <show-errors
                      :control="$v.basicSettingInfo.email"
                      :field="$t('MAILADDRESS')"
                    />
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12">
                  <b-col cols="12" class="mt-1">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="font-weight-bold">{{
                      $t("Headquarterslocation")
                    }}</label>
                  </b-col>
                  <b-col id="options" cols="12" xl="2" class="mt-3">
                    <b-form-select
                      v-model="selectedOffice"
                      :plain="true"
                      :options="officeOptions"
                      value-field="item"
                      text-field="name"
                      disabled-field="notEnabled"
                      class="optionClass"
                    ></b-form-select>
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12">
                  <b-col cols="12">
                    <b-form-input
                      v-model.trim="$v.basicSettingInfo.officeAddress.$model"
                      :placeholder="$t('HEADQUARTERSCITY')"                      
                      maxlength="300"
                      class="textFieldClass"
                    ></b-form-input>
                    <show-errors
                      :control="$v.basicSettingInfo.officeAddress"
                      :field="$t('HeadquartersAddress')"
                    />
                  </b-col>
                </b-col>
              </b-row>

              <br />
              <br />
              <b-row class="mt-1">
                <b-col
                  cols="12"
                  xl="6"
                  class="mt-1 d-flex align-items-center justify-content-end text-right"
                >
                  <b-button
                    @click="goToAccountInfo"
