                      class="fa fa-caret-left fa-2x mr-2"
                      aria-hidden="true"
                    ></i>
                    {{ $t("TOACCOUNTINFORMATION") }}
                  </b-button>
                </b-col>
                <b-col cols="12" xl="6" class="text-left mt-1">
                  <b-button
                    @click="clientBasicInfoCheck"
                    variant="primary"
                    class="btnWidth"
                    >{{ $t("TOENTERPRISESIZE") }}
                    <i v-if="updBusy" class="fa fa-spinner fa-spin" />
                  </b-button>
                </b-col>
              </b-row>
            </b-form>
          </b-tab>

          <b-tab :title-link-class="linkClass(2)" :title="$t('ENTERPRISESIZE')">
            <b-form>
              <b-row>
                <b-col cols="12" xl="12" class="ml-3">
                  <b-button
                    variant="primary"
                    class="px-4 required-btnAny align-content-center"
                    >{{ $t("REQUIREDBUTTON") }}</b-button
                  >&nbsp;&nbsp;
                  <label class="highLightHeaderLabel">{{
                    $t("Industry")
                  }}</label>
                </b-col>
                <b-col cols="11" xl="3" class="ml-2">
                  <b-form-select
                    v-model.trim="companySettingInfo.industry"
                    :plain="true"
                    :options="industryOptions"
                    value-field="value"
                    text-field="text"
                    disabled-field="notEnabled"
                    class="optionClass"
                  ></b-form-select>
                  <show-errors
                    :control="$v.companySettingInfo.industry"
                    :field="$t('Industry')"
                  />
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12" xl="6">
                  <b-col cols="12" class="mt-2">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("YEAR1")
                    }}</label>
                  </b-col>
                  <b-col id="options" cols="12" class="mt-3">
                    <b-form-select
                      v-model.trim="companySettingInfo.year"
                      :plain="true"
                      :options="options"
                      :disabled="onRowDisabled"
                      value-field="item"
                      text-field="name"
                      class="optionClass"
                      disabled-field="notEnabled"
                    ></b-form-select>
                    <show-errors
                      :control="$v.companySettingInfo.year"
                      :field="$t('YEAR1')"
                    />
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6">
                  <b-col cols="12" class="mt-2">
                    <b-button variant="primary" class="px-4 required-btnAny">{{
                      $t("REQUIREDBUTTON")
                    }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("EmployeeNumber")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-3">
                    <b-form-input
                      v-model.trim="companySettingInfo.employee"
                      :placeholder="'1,000' + $t('PEOPLE')"                      
                      maxlength="7"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.companySettingInfo.employee"
                      :field="$t('EmployeeNumber')"
                    />
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12" xl="6">
                  <b-col cols="12" class="mt-2">
                    <b-button variant="primary" class="px-4 required-btnAny">{{
                      $t("REQUIREDBUTTON")
                    }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel"
                      >{{ $t("Capital") }}
                    </label>
                  </b-col>
                  <b-col cols="12" class="mt-3">
                    <b-form-input
                      v-model.trim="companySettingInfo.capital"
                      :placeholder="'5,000,000' + $t('YEN')"                      
                      maxlength="14"

                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.companySettingInfo.capital"
                      :field="$t('Capital')"
                    />
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6">
                  <b-col cols="12" class="mt-2">
                    <b-button variant="primary" class="px-4 required-btnAny">{{
                      $t("REQUIREDBUTTON")
                    }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("Revenue")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-3">
                    <b-form-input
                      v-model.trim="companySettingInfo.sales"
                      :placeholder="'10,000,000' + $t('YEN')"                      
                      maxlength="14"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.companySettingInfo.sales"
                      :field="$t('Revenue')"
                    />
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12" xl="6">
                  <b-col cols="12">
                    <b-button variant="primary" class="px-4 required-btnAny">{{
                      $t("REQUIREDBUTTON")
                    }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("FloorSpace")
                    }}</label>
                  </b-col>
                  <b-col cols="12">
                    <b-form-input
                      v-model.trim="companySettingInfo.floorSize"
                      :placeholder="'100' + $t('PING')"                      
                      maxlength="9"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.companySettingInfo.floorSize"
                      :field="$t('FloorSpace')"
                    />
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6">
                  <b-col cols="12">
                    <b-button variant="primary" class="px-4 required-btnAny">{{
                      $t("REQUIREDBUTTON")
                    }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("NumberOfStores")
                    }}</label>
                  </b-col>
                  <b-col cols="12">
                    <b-form-input
                      v-model.trim="companySettingInfo.storeNo"
                      :placeholder="'50' + $t('STOREUNIT')"                      
                      maxlength="7"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.companySettingInfo.storeNo"
                      :field="$t('NumberOfStores')"
                    />
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row class="mt-1">
                <b-col
                  cols="12"
                  xl="6"
                  class="text-right d-flex align-items-center justify-content-end"
                >
                  <b-button
                    @click="goToBasicSettingInfo"
                    variant="primary"
                    class="btnWidth mr-2 d-flex align-items-center justify-content-center disableBtn"
                    ><i
                      class="fa fa-caret-left fa-2x mr-2"
                      aria-hidden="true"
                    ></i>
                    {{ $t("TOBASICINFORMATION") }}</b-button
                  >
                </b-col>
                <b-col cols="12" xl="6" class="text-left">
                  <b-button
                    @click="createCompanyInfoData"
                    variant="primary"
                    class="btnWidth"
                    >{{ $t("TOOTHER") }}
                    <i v-if="updBusy" class="fa fa-spinner fa-spin" />
                  </b-button>
                </b-col>
              </b-row>
              <br />
            </b-form>
          </b-tab>
          <b-tab :title-link-class="linkClass(3)" :title="$t('OTHER')">
            <b-form id="tab3">
              <br />
              <b-row class="no-gutters">
                <b-col cols="12">
                  <b-button
                    variant="outline-primary"
                    class="px-4 required-btnAny1"
                    >{{ $t("Any") }}</b-button
                  >&nbsp;&nbsp;
                  <label class="highLightHeaderLabel">{{
                    $t("StartMonth")
                  }}</label>
                </b-col>
                <b-col id="monthSelect" cols="10" xl="2" class="mt-3">
                  <b-form-select
                    :plain="true"
                    v-model="selectedMonth"
                    :options="monthOptions"
                    value-field="item"
                    text-field="name"
                    class="optionClass"
                    disabled-field="notEnabled"
                  ></b-form-select>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12">
                  <b-button
                    variant="outline-primary"
                    class="px-4 required-btnAny1"
                    >{{ $t("Any") }}</b-button
                  >&nbsp;&nbsp;
                  <label class="highLightHeaderLabel">{{
                    $t("ComPanyIcon")
                  }}</label>
                </b-col>
                <b-col cols="12" xl="1">
                  <div id="thumbnailLogoClient" v-if="imagePath != ''">
                    <img
                      v-if="imagePath"
                      v-bind:src="imagePath"
                      @click="onPickFile()"                      
                      class="thumbnailLogo"
                      alt="client_icon"
                    />
                    <input
                    id="fileupload"
                    ref="fileupload"
                    @change="onFileChange"
                    type="file"
                    name="fileupload"
                    hidden
                  />
                  </div>
                  <div v-else>
                    <img
                      @click="onPickFile()"
                      src="@/assets/browse_image.svg"
                      class="thumbnailLogo"
                      alt="client_icon"
                    />
                    <input
                    id="fileupload"
                    ref="fileupload"
                    @change="onFileChange"
                    type="file"
                    name="fileupload"
                    hidden
                  />
                  </div>
                </b-col>
                <div class="mt5 ml4 thumbnailLogo1">
                  <b-button
                    id="fileSelect"
                    @click="onPickFile()"                    
                    class="camCls"
                  >
                  </b-button>
                  <input
                    id="fileupload"
                    ref="fileupload"
                    @change="onFileChange"
                    type="file"
                    name="fileupload"
                    hidden
                  />
                </div>
                <b-col cols="10" xl="12">
                  <p class="text-danger">{{ fileErrorMessage }}</p>
                </b-col>
              </b-row>
              <b-row>
                <b-col cols="12" xl="3">
                  <b-link
                    @click="deleteFile"
                    href="javascript:;"
                    class="card-link float-left link mt-2"
                  >
                    <u>{{ $t("DeleteIcon") }}</u>
                  </b-link>
                </b-col>
              </b-row>
              <b-row class="mt-3">
                <b-col
                  cols="12"
                  xl="2"
                  class="d-flex align-items-center justify-content-end"
                >
                  <b-button
                    @click="goToCompanySizeInfo"
                    variant="primary"
                    class="btnWidth mr-2 d-flex align-items-center justify-content-center disableBtn"
                    ><i
                      class="fa fa-caret-left fa-2x mr-2"
                      aria-hidden="true"
                    ></i>
                    {{ $t("TOENTERPRISESIZE") }}
                  </b-button>
                </b-col>
                <b-col cols="12" xl="2">
                  <b-button
                    @click="createClientData"                  
                    :disabled="loading"
                    variant="primary"
                    class="btnWidth"
                    >{{ $t("REGISTER") }}
                    <i v-if="loading" class="fa fa-spinner fa-spin" />
                  </b-button>
                </b-col>
              </b-row>
              <br />
            </b-form>
          </b-tab>
        </b-tabs>
      </b-card>
    </div>
  </div>
</template>

<script>
import { required, email, numeric, minLength } from "vuelidate/lib/validators";
import { Settings } from "luxon";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
import ShowErrors from "@/components/ShowErrors";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";
import { Config } from "@/shared/config";
import { getDateHourMinute } from "@/shared/utils";
export default {
  name: "CreateIndex",
  components: {
    ShowErrors,
  },
  data() {
    return {
      baseDOMAIN: Config.apiDomain,
      imagePath: "",
      selectedIndustry: "",
      industryOptions: [],
      industryTitle: "",
      onRowDisabled: false,
      tabIndex: 0,
      treeOptions: {},
      tableYear: "",
      companySizeName: "",
      loading: false,
      allTreeData: [],
      treeData: [],
      text: "",
      file: "",
      fileName: "",
      files: [],
      fileErrorMessage: "",
      validateLoginId: "",
      officeOptions: [],
      options: [],
      monthOptions: [
        { item: "", name: "月" },
        { item: "1", name: "1月" },
        { item: "2", name: "2月" },
        { item: "3", name: "3月" },
        { item: "4", name: "4月" },
        { item: "5", name: "5月" },
        { item: "6", name: "6月" },
        { item: "7", name: "7月" },
        { item: "8", name: "8月" },
        { item: "9", name: "9月" },
        { item: "10", name: "10月" },
        { item: "11", name: "11月" },
        { item: "12", name: "12月" },
      ],
      selectedOffice: "1",
      selectedYear: new Date().getFullYear(),
      clientOtherInfoNo: "",
      selectedMonth: "",
      isAddSelected: true,
      isRowSelected: false,
      tableData: [],
      selectModeTable: "single",
      selectedRow: [],
      accountSettingInfo: {
        loginId: "",
        password: "",
      },
      basicSettingInfo: {
        companyName: "",
        personInCharge: "",
        email: "",
        officeAddress: "",
        phoneNo: "",
      },

      companySettingInfo: {
        year: "",
        employee: "",
        capital: "",
        sales: "",
        floorSize: "",
        storeNo: "",
        industry: "",
      },
      currentMonth: new Date().getMonth() + 1,
      autoProposal: "",
      autoProposalDate: "",
      cntBaseInfoDate: "",
      companySizeInfoData: [],
      companyIndustryNo: "",
      isBusy: false,
      addBusy: false,
      updBusy: false,
      errorMessage: "",
      currentSelectedIndustryItem: "",
    };
  },
  computed: {
    ...mapGetters({
      selected_industry_item: "getSelectedIndustryItem",
    }),
  },
  validations: {
    accountSettingInfo: {
      loginId: {
        required,
        characterMinLength: minLength(8),
      },
      password: { required, minLength: minLength(8) },
    },
    basicSettingInfo: {
      companyName: { required },
      personInCharge: { required },
      email: { required, email },
      officeAddress: { required },
      phoneNo: {
        required,
        phoneNumber: numeric,
        phoneMinLength: minLength(10),
      },
    },
    companySettingInfo: {
      year: { required },
      capital: { required },
      employee: { required },
      sales: { required },
      floorSize: { required },
      storeNo: { required },
      industry: { required },
    },
  },
  watch: {
    "companySettingInfo.capital"(newVal) {
      //to work with changes in "myArray"
      this.commaSeparatedCapital(newVal);
    },
    "companySettingInfo.employee"(newVal) {
      //to work with changes in "myArray"
      this.commaSeparatedEmployee(newVal);
    },
    "companySettingInfo.floorSize"(newVal) {
      //to work with changes in "myArray"
      this.commaSeparatedFloorSize(newVal);
    },
    "companySettingInfo.storeNo"(newVal) {
      //to work with changes in "myArray"
      this.commaSeparatedStoreNo(newVal);
    },
    "companySettingInfo.sales"(newVal) {
      //to work with changes in "myArray"
      this.commaSeparatedRevenue(newVal);
    },
    "accountSettingInfo.loginId"(newVal) {
      if (
        this.accountSettingInfo.loginId == "" ||
        this.accountSettingInfo.loginId.length < 8
      ) {
        console.log(
          "pPPPPPPPPPP",
          this.accountSettingInfo.loginId.length,
          newVal
        );
        this.validateLoginId = "";
      }
    },
  },
  mounted() {
    this.prefectureData();
    this.getTreeData();
  },

  created() {},
  methods: {
    commaSeparatedCapital(newVal) {
      console.log(newVal, "val");
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.capital = data ? data + this.$t("YEN") : "";  //.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparatedEmployee(newVal) {
      console.log(newVal, "val");
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.employee = data ? data + this.$t("PEOPLE") : ""; //.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparatedFloorSize(newVal) {
      console.log(newVal, "val");
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.floorSize = data ? data + this.$t("PING") : ""; //.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparatedStoreNo(newVal) {
      console.log(newVal, "val");
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.storeNo = data ? data + this.$t("STOREUNIT") : ""; //.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparatedRevenue(newVal) {
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.sales = data ? data + this.$t("YEN") : ""; //newVal.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparater(val) {
      return val.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    /**
     * @description function to fetch available industry and updating to store
     * @param data with ["01", "02"]
     */
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMINDUSTRY,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "industry_kbn",
              value: ["01", "02"],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.allTreeData = responseData;
        if (this.selected_industry_item.industry_nm) {
          //this.companySettingInfo.industry = this.selected_industry_item.industry_nm;
        } else {
          this.allTreeData.forEach((item) => {
            if (
              this.companyIndustryNo == item.industry_no &&
              item.industry_kbn == 2
            ) {
              //this.companySettingInfo.industry = item.industry_nm;
            }
          });
        }
        let mapData = [];
        let sortData = this._.sortBy(responseData, "industry_big_cd");
        let groupedData = this._.groupBy(sortData, "industry_big_cd");
        this.industryOptions.push({ text: "業種を選ぶ", value: "" });
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          let parentData = [];
          groupedData[key].forEach((item) => {
            if (item.industry_kbn == 1) {
              let parentNode = item.industry_nm;
              parentData.push(parentNode);
            } else {
              let bindChildData = {
                text: item.industry_nm,
              };
              let industryData = {
                text: item.industry_nm,
                value: item.industry_no,
              };
              this.industryOptions.push(industryData);
              childrenArray.push(bindChildData);
            }
          });
          let name = ":" + parentData[0];
          if (key == "A") {
            let bindTreeData = {
              text: key + name,
              state: { expanded: true },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          } else {
            let bindTreeData = {
              text: key + name,
              state: { expanded: false },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          }
        });
      }
    },
    /**
     * @description function to fetch available prefectures data
     * @returns assign to dropDown fields
     */
    async prefectureData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMGENERALPURPOSE,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "key",
              value: "PREF",
            },
          ],
        }
      );
      let responseData = data.data;
      let codevalue = [];
      let value = [];
      let optionData = [];
      responseData.forEach((item) => {
        codevalue.push(parseInt(item.code));
        value = item.value_string_small;
        let i = 1;
        let insert = 0;
        if (codevalue[insert] == i) {
          optionData.push(value);
          i = i + 1;
        }
      });

      for (let index = 0; index < optionData.length; index++) {
        this.officeOptions.push({
          item: codevalue[index],
          name: optionData[index],
        });
      }
      let startyear = parseInt(new Date().getFullYear()) - 10;
      let endYear = parseInt(new Date().getFullYear()) + 10;
      this.options.push({ item: "", name: this.$t("YEAR") });
      for (let index = startyear; index <= endYear; index++) {
        this.options.push({ item: index, name: index + this.$t("YEAR") });
      }
    },
    /**
     * @description function to remove japanese character from field value
     * @param {any} string
     * @param {any} value
     * @returns string value
     */
    removeChar(value) {
      try {
        return value.substring(1).replace(/[^0-9.-]+/g, "");
      } catch (error) {
        return 0;
      }
    },
    /**
     * @description function to convert japanese currency format to number
     * @param {any} int
     * @param {any} value
     * @returns int value
     */
    NumberConvert(value) {
      try {
        return value.replace(/[^0-9.-]+/g, "");
      } catch (error) {
        console.log(error);
      }
    },
    /**
     * @description function to convert number format to japanese currency format
     * @param {any} int
     * @param {any} value
     * @returns string currency value
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    onPickFile() {
      document.getElementById("fileupload").click();
    },
    async createClientData() {
      this.$v.companySettingInfo.$touch();
      this.$v.basicSettingInfo.$touch();
      this.$v.accountSettingInfo.$touch();

      if (this.$v.accountSettingInfo.$invalid) {
        this.tabIndex = 0;
        return false;
      }
      if (this.$v.basicSettingInfo.$invalid) {
        this.tabIndex = 1;
        return false;
      }
      if (this.$v.companySettingInfo.$invalid) {
        this.tabIndex = 2;
        return false;
      }

      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTACCOUNT,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_login_id",
              value: this.accountSettingInfo.loginId,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        if (responseData.length > 0) {
          this.validateLoginId = this.$t("LOGINIDALREADYEXISTS");
        } else {
          this.validateLoginId = "";
        }
      }
      else{
        this.tabIndex = 0;
        return false;
      }

      if (this.validateLoginId != "") {
        this.tabIndex = 0;
        return false;
      }
      if (!this.$v.companySettingInfo.$invalid && this.fileErrorMessage == "") {
        let clientAccount = {
          client_login_id: this.accountSettingInfo.loginId,
          client_login_pass: this.accountSettingInfo.password,
        };
        let clientBaseInfo = {
          client_mail: this.basicSettingInfo.email,
          client_name: this.basicSettingInfo.companyName,
          client_person_name: this.basicSettingInfo.personInCharge,
          client_tel: this.basicSettingInfo.phoneNo,
          client_main_office_kbn: this.selectedOffice,
          del_flg: false,
          create_dt: getDateHourMinute(new Date()),
          update_dt: getDateHourMinute(new Date()),
          created_at: getDateHourMinute(new Date()),
          updated_at: getDateHourMinute(new Date()),
          client_main_office_addr: this.basicSettingInfo.officeAddress,
        };
        let clientCompanySizeInfo = {
          info_year: this.companySettingInfo.year,
          company_size: this.companysize,
          industry_no: this.companySettingInfo.industry,
          employee_number: parseInt(this.NumberConvert((this.companySettingInfo.employee).slice(0,-1))),
          capital: parseInt(
            this.NumberConvert((this.companySettingInfo.capital).slice(0,-1))
          ),
          revenue: parseInt(this.NumberConvert((this.companySettingInfo.sales).slice(0,-1))),
          floor_space: parseInt(this.NumberConvert((this.companySettingInfo.floorSize).slice(0,-1))),
          number_of_stores: parseInt(this.NumberConvert((this.companySettingInfo.storeNo).slice(0,-2))),
          del_flg: false,
          create_dt: getDateHourMinute(new Date()),
          update_dt: getDateHourMinute(new Date()),
          created_at: getDateHourMinute(new Date()),
          updated_at: getDateHourMinute(new Date()),
        };
        let clientOtherInfo = {
          year_start_month: this.selectedMonth,
          auto_proposal: false,
          auto_proposal_dt: getDateHourMinute(new Date()),
          icon_path: "null",
          icon_file_name: "null",
          del_flg: false,
          create_dt: getDateHourMinute(new Date()),
          update_dt: getDateHourMinute(new Date()),
        };
        this.loading = true;
        let payload = [
          {
            table: "m_client_account",
            primary_key: "client_account_no",
            data: [
              {
                client_login_id: clientAccount.client_login_id,
                client_login_pass: clientAccount.client_login_pass,
                create_id: localStorage.getItem("personID"),
                update_id: localStorage.getItem("personID"),
              },
            ],
            dependency: [],
          },
          {
            table: "m_client_base_info",
            primary_key: "client_no",
            data: [
              {
                client_mail: clientBaseInfo.client_mail,
                client_name: clientBaseInfo.client_name,
                client_person_name: clientBaseInfo.client_person_name,
                client_tel: clientBaseInfo.client_tel,
                client_main_office_kbn: clientBaseInfo.client_main_office_kbn,
                create_id: localStorage.getItem("personID"),
                update_id: localStorage.getItem("personID"),
                client_main_office_addr: clientBaseInfo.client_main_office_addr,
              },
            ],
            dependency: [
              {
                entity_name: "m_client_account",
                entity_column: "client_account_no",
              },
            ],
          },
          {
            table: "m_client_company_size_info",
            primary_key: "company_size_info_no",
            data: [
              {
                info_year: clientCompanySizeInfo.info_year,
                company_size: clientCompanySizeInfo.company_size,
                industry_no: clientCompanySizeInfo.industry_no,
                employee_number: clientCompanySizeInfo.employee_number,
                capital: clientCompanySizeInfo.capital,
                revenue: clientCompanySizeInfo.revenue,
                floor_space: clientCompanySizeInfo.floor_space,
                number_of_stores: clientCompanySizeInfo.number_of_stores,
                create_id: localStorage.getItem("personID"),
                update_id: localStorage.getItem("personID"),
              },
            ],
            dependency: [
              {
                entity_name: "m_client_base_info",
                entity_column: "client_no",
              },
            ],
          },
          {
            table: "m_client_other_info",
            primary_key: "client_other_info_no",
            data: [
              {
                year_start_month: clientOtherInfo.year_start_month,
                icon_path: null,
                icon_file_name: null,
                auto_proposal_dt: clientOtherInfo.auto_proposal_dt,
                create_id: localStorage.getItem("personID"),
                update_id: localStorage.getItem("personID"),
              },
            ],
            dependency: [
              {
                entity_name: "m_client_base_info",
                entity_column: "client_no",
              },
            ],
          },
        ];
        const { data } = await BasicInfoApis.postAPI(
          "master/createinmultitablewithdependentids",
          payload
        );
        this.loading = false;
        if (data.success) {
          let responseData = data.data;
          if (responseData) {
            if (this.files.length > 0) {
              this.fileUpload(
                responseData.m_client_other_info.client_other_info_no
              );
            }
            else{
                this.$router.push({ name: "client_list" });
            }
          } else {
            console.log("empty Data response");
          }
        } else {
          console.log("Insertion Failed");
        }
      }
    },
    /**
     * @description function to check whether client basic information data to update or Create
     * After checking, calls respective API(update or create)
     */
    async accountInfoCheck() {
      this.$v.accountSettingInfo.$touch();
      if (!this.$v.accountSettingInfo.$invalid) {
        this.tabIndex = 1;
      }
    },
    clientBasicInfoCheck() {
      this.$v.basicSettingInfo.$touch();
      this.$v.accountSettingInfo.$touch();
      if (this.$v.accountSettingInfo.$invalid || this.validateLoginId != "") {
        this.tabIndex = 0;
        return false;
      }
      if (!this.$v.basicSettingInfo.$invalid) {
        this.tabIndex = 2;
      }
    },
    goToAccountInfo() {
      this.tabIndex = 0;
    },
    goToBasicSettingInfo() {
      this.tabIndex = 1;
    },
    goToCompanySizeInfo() {
      this.tabIndex = 2;
    },
    /**
     * @description function to check client company information for logged in account is exist or not
     * @param {int} client_account_no
     * @param {string} yearInfo
     * @returns {object}
     */
    async createCompanyInfoData() {
      this.$v.companySettingInfo.$touch();
      this.$v.basicSettingInfo.$touch();
      this.$v.accountSettingInfo.$touch();

      if (this.$v.accountSettingInfo.$invalid || this.validateLoginId != "") {
        this.tabIndex = 0;
        return false;
      }
      if (this.$v.basicSettingInfo.$invalid) {
        this.tabIndex = 1;
        return false;
      }
      if (!this.$v.companySettingInfo.$invalid) {
        this.tabIndex = 3;
      }
    },
    /**
     * @description function to check and validate browsed files
     * @param {file} data
     */
    onFileChange(event) {
      let fileSize = 0;
      for (var i = 0; i < event.target.files.length; i++) {
        var ext = event.target.files[i].name.split(".").pop();
        fileSize += Math.round(event.target.files[i].size / 1024);
        if (
          ext == "jpg" ||
          ext == "png" ||
          ext == "jpeg" ||
          ext == "JPG" ||
          ext == "PNG" ||
          ext == "JPEG"
        ) {
          if (fileSize > 1024) {
            this.fileErrorMessage = this.$t("MAXICONSIZE");
          } else {
            for (let i = 0; i < event.target.files.length; i++) {
              this.files.push(event.target.files[i]);
            }
            this.fileName = event.target.files[0].name;
            let file = event.target.files[0];
            this.imagePath = URL.createObjectURL(file);
            this.fileErrorMessage = "";
          }
        } else {
          this.fileErrorMessage = this.$t("UPLOADEXCECUTABLEFILES");
        }
        fileSize += Math.round(event.target.files[i].size / 1024);
      }
    },
    /**
     * @description function to upload validated browsed files
     * @param {formData} data
     */
    async fileUpload(clientOtherInfoNo) {
      this.fileErrorMessage = "";
      var formData = new FormData();
      for (let i = 0; i < this.files.length; i++) {
        formData.append("attachment", this.files[i]);
      }
      formData.append("client_other_info_no", clientOtherInfoNo);
      formData.append("update_id", localStorage.getItem("personID"));
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONUPDATECLIENTOTHERINFOICON,
        formData
      );
      if (data.success) {
        console.log("File uploaded successfully");
        this.$router.push({ name: "client_list" });
      }
    },
    /**
     * @description function to delete the existing files for client other information table
     * @param {array} data
     * @param {array} conditions
     */
    async deleteFile() {
      this.fileName = "";
      this.imagePath = "";
      this.fileErrorMessage = "";
      this.files = [];
      this.$refs.fileupload.value = null;
    },

    linkClass(idx) {
      if (this.tabIndex === idx) {
        return ["bg-white", "text-black"];
      } else {
        return ["bg-primary", "text-black"];
      }
    },
  },
};
</script>
